import { createSlice } from "@reduxjs/toolkit";

import { LoadingState } from "src/utils/constants";

export interface GallerySlice {
  selectedCountry: String;
  selectedRegion: String;
  selectedAll: Boolean;
  countries: [];
  regions: [];
  plates: [];
  photos: {};
  allPhotos: [];
  chosen: String;
  photosLoading: LoadingState;
}

const initialState: GallerySlice = {
  selectedCountry: null,
  selectedRegion: null,
  selectedAll: false,
  countries: [],
  regions: [],
  plates: [],
  photos: {},
  allPhotos: [],
  chosen: null,
  photosLoading: LoadingState.idle,
};

export const gallerySlice = createSlice({
  name: "gallery",
  initialState,
  reducers: {
    setRegions(state, action) {
      state.regions = action.payload;
      return state;
    },
    setCountries(state, action) {
      state.countries = action.payload;
      return state;
    },
    setSelectedRegion(state, action) {
      state.selectedCountry = null;
      state.selectedRegion = { name: action.payload };
      state.selectedAll = false;
      return state;
    },
    setSelectedCountry(state, action) {
      state.selectedCountry = { name: action.payload };
      state.selectedRegion = null;
      state.selectedAll = false;
      return state;
    },
    setSelectedAll(state, action) {
      state.selectedCountry = null;
      state.selectedRegion = null;
      state.selectedAll = !state.selectedAll;
      return state;
    },
    setPhotosLoading(state, action) {
      state.photosLoading = action.payload;
      return state;
    },
    setAllPhotos(state, action) {
      state.allPhotos = action.payload;
      return state;
    },
    setPhotos(state, action) {
      state.allPhotos = action.payload;
      return state;
    },
    setPhotosByRegion(state, action) {
      state.chosen = action.payload;
      let photosTmp = [];

      state.allPhotos.map((photo) => {
        if (photo.region === action.payload) {
          photosTmp.push(photo.name);
        }
      });

      let photos = {};
      photos[action.payload] = photosTmp;
      state.photos = photos;
      return state;
    },
    setPhotosByCountry(state, action) {
      state.chosen = action.payload;
      let photosTmp = [];

      state.allPhotos.map((photo) => {
        if (photo.country === action.payload) {
          photosTmp.push(photo.name);
        }
      });

      let photos = {};
      photos[action.payload] = photosTmp;
      state.photos = photos;
      return state;
    },
    setPhotos(state, action) {
      state.chosen = "all";
      let photosTmp = [];

      state.allPhotos.map((photo) => {
        photosTmp.push(photo.name);
      });

      let photos = {};
      photos["all"] = photosTmp;
      state.photos = photos;
      return state;
    },
    clearChosen(state, action) {
      state.chosen = {};
      state.photos = {};
      return state;
    },
  },
  extraReducers: (builder) => {},
});

export const {
  setSelectedRegion,
  setSelectedCountry,
  setPhotosLoading,
  clearChosen,
  setRegions,
  setCountries,
  setAllPhotos,
  setPhotosByRegion,
  setPhotosByCountry,
  setSelectedAll,
  setPhotos,
} = gallerySlice.actions;

export default gallerySlice.reducer;
